import React from 'react';
import {NavLink} from "react-router-dom";

const Navbar = () => {
    return(
         <>
                                     
        <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
  <div className="container-fluid">
    <NavLink activeClassName='active' className="navbar-brand" to="#">FootballFeeds</NavLink>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
        <li className="nav-item">
          <NavLink activeClassName='active' className="nav-href"  to="/">Home </NavLink>
        </li>
        <li className="nav-item">
          <NavLink activeClassName='active' className="nav-href"  to="/players">Players</NavLink>
        </li>
        <li className="nav-item">
          <NavLink activeClassName='active' className="nav-href"  to="/clubs">Clubs</NavLink>
        </li>
        <li className="nav-item">
          <NavLink activeClassName='active' className="nav-href"  to="/contact">Contact</NavLink>
        </li>
        
        
       
      </ul>
      
    </div>
  </div>
</nav>
        </>
    );

};

export default Navbar
